// Theme variables are globally available via CSS custom properties
.form-test-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
  
  // Light theme 기본 적용
  background-color: #fff8f0;
  
  &.light-theme {
    background-color: var(--form-bg-secondary);
  }
  
  &.dark-theme {
    background-color: var(--form-bg-secondary);
  }

  .header-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    flex-wrap: wrap;
    gap: 20px;

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .header-actions {
    button {
      mat-icon {
        margin-right: 8px;
      }
    }
  }

  h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    font-weight: 700;
  }

  .light-theme h1 {
    background: linear-gradient(135deg, #f97316 0%, #ea580c 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .dark-theme h1 {
    background: linear-gradient(135deg, #10b981 0%, #059669 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .subtitle {
    font-size: 1.2rem;
    margin-bottom: 0;
  }

  .light-theme .subtitle {
    color: #78716c;
  }

  .dark-theme .subtitle {
    color: #cbd5e1;
  }

  .form-section {
    margin-bottom: 30px;
    padding: 20px;
    background-color: var(--form-bg-primary);
    border: 1px solid var(--form-border-color);
    border-radius: 8px;
    box-shadow: 0 2px 8px var(--form-shadow);
    transition: all 0.3s ease;

    &:hover {
      box-shadow: 0 4px 16px var(--form-shadow);
    }

    mat-card-header {
      margin-bottom: 20px;
      border-bottom: 2px solid var(--form-border-color);
      padding-bottom: 10px;
    }

    mat-card-title {
      font-size: 1.5rem;
      color: var(--form-text-primary);
      font-weight: 600;
    }
  }

  .submit-button {
    margin-top: 20px;
    text-align: right;

    button {
      min-width: 120px;
    }
  }

  .result-box {
    margin-top: 20px;
    padding: 15px;
    background-color: var(--form-success-light);
    border-radius: 8px;
    border-left: 4px solid var(--form-success-color);

    h4 {
      margin: 0 0 10px 0;
      color: var(--form-success-color);
    }

    pre {
      margin: 0;
      padding: 10px;
      background-color: var(--form-field-bg);
      border-radius: 4px;
      overflow-x: auto;
      font-size: 0.9rem;
      line-height: 1.5;
      color: var(--form-text-primary);
      font-family: 'Courier New', monospace;
    }
  }

  .info-section {
    background-color: var(--form-bg-tertiary);
    padding: 20px;
    border-radius: 8px;
    border: 1px solid var(--form-border-color);

    h3 {
      color: var(--form-primary-color);
      margin-top: 20px;
      margin-bottom: 10px;
      font-size: 1.2rem;

      &:first-child {
        margin-top: 0;
      }
    }

    pre {
      background-color: var(--form-field-bg);
      color: var(--form-text-primary);
      padding: 15px;
      border-radius: 8px;
      border: 1px solid var(--form-border-color);
      overflow-x: auto;
      font-size: 0.9rem;
      line-height: 1.5;

      code {
        font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
      }
    }

    .feature-list {
      margin-top: 20px;

      ul {
        list-style: none;
        padding: 0;

        li {
          padding: 8px 0;
          font-size: 1rem;
          color: var(--form-text-primary);

          &::before {
            content: '';
            display: inline-block;
            width: 6px;
            height: 6px;
            background-color: var(--form-success-color);
            border-radius: 50%;
            margin-right: 10px;
          }
        }
      }
    }
  }
}

// 반응형 디자인
@media (max-width: 768px) {
  .form-test-container {
    padding: 10px;

    h1 {
      font-size: 2rem;
    }

    .subtitle {
      font-size: 1rem;
    }

    .form-section {
      padding: 15px;
    }
  }
}
